a{
    text-decoration: none;
    color: aliceblue;
    width: 100%;
}

.nav{
    /* background-color: red; */
    padding: 10px;
}

.nav::before{
    content: "";
    display: inline-block;
    width: 40px;
    height: 5px;
    background-color: white;
}

.nav::after{
    content: "";
    display: inline-block;
    width: 40px;
    height: 5px;
    background-color: white;
    position: absolute;
    right: 10px;
    top: 35px;
}
